import '../../App.css';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import 'date-fns';
import TextFieldNumber from "../../components/TextFieldNumber";
import SectionBlockMini from "../../components/SectionBlockMini";
import DatePicker from "../../components/DatePicker";
import Button from "../../components/Button";
import HeaderBlock from "../../components/HeaderBlock";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },

    hrDown: {
        margin: "20px auto",
        borderTop: "1px solid rgba(0, 0, 0, 0.1)",
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        height: 0,
        maxWidth: "45%",
        [theme.breakpoints.down('600')]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up('xs')]: {
            maxWidth: "80%",
        },
        [theme.breakpoints.up('md')]: {
            maxWidth: "60%",
        },
        [theme.breakpoints.up('lg')]: {
            maxWidth: "40%",
        },
    },
    title: {
        fontSize: 15,
        fontWeight: "bold",
        textAlign: "left",
    },

    subtitle: {
        fontSize: 12,
        fontWeight: "bold",
        color: 'rgb(113,113,113)',
        textAlign: "left",
    },

    date_text: {
        fontSize: 12,
        color: 'rgb(113,113,113)',
        textTransform: 'uppercase',
    },

    date_value: {
        fontSize: 18,
        color: 'rgb(113,113,113)',
        fontWeight: "bold",
    },

}));

function VolumeWorkPage({works}) {
    const classes = useStyles();

    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const idFromUrl = urlParams.get('id');

    return (
        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <HeaderBlock titleHeader="Объем выполненных работ"/>
                        <hr className={classes.hrDown}/>
                        <Grid container alignItems="center" direction="column">
                            <Grid item xs={9} lg={5}>
                                <div className={classes.title}>{works[idFromUrl].title}</div>
                            </Grid>
                            <Grid item xs={9} lg={5}>
                                <div className={classes.subtitle}>{works[idFromUrl].subtitle}</div>
                            </Grid>
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <DatePicker/>
                        <TextFieldNumber/>
                        <Button/>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="center">
                            <SectionBlockMini
                                title={'Осталось к выполнению'}
                                value={(works[idFromUrl].workDonePart2-works[idFromUrl].workDonePart1).toFixed(2)}
                            />
                            <SectionBlockMini
                                title={'Ср. производительность'}
                                value={works[idFromUrl].productivity}
                            />
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="center" direction="column">
                            <div className={classes.date_text}>Прогнозируемая дата завершения</div>
                            <div className={classes.date_value}>{works[idFromUrl].date}</div>
                        </Grid>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}
export default VolumeWorkPage;
