import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({

    root: {
        flexGrow: 1,
    },

    block_value: {
        background: 'rgb(47,128,237)',
        borderRadius: "10px",
        padding:  theme.spacing(1),
        display: "flex",
        justifyContent: "center",
        width: "100%",
    },

    value : {
        fontSize: "18px",
        fontWeight: 'bold',
        color: 'rgb(255,255,255)',
    },

    done1: {
        fontSize: 14,
        color: 'rgb(22,111,255)',
        textTransform: 'uppercase',
        fontWeight: 'bold',
        verticalAlign: "middle",
        textAlign: "left",
        padding: 3,
        paddingBottom: 7,
    },

    done2: {
        fontSize: 14,
        color: 'rgb(22,111,255)',
        textTransform: 'uppercase',
        fontWeight: 'bold',
        verticalAlign: "middle",
        textAlign: "left",
        padding: 3,
    },

    blockHeader: {
        background: 'rgb(229,239,255)',
        borderRadius: "15px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        padding: theme.spacing(1),
        margin: theme.spacing(1),
    },

}));

function NeedForSpeed ({ workDonePart1, workDonePart2, deadline }) {
    const classes = useStyles();
    return (
        <Grid container className={classes.blockHeader} direction="column" xs={6} sm={3} md={2} lg={1}>
            <Grid item justifyContent="center" alignItems="center">
                <div className={classes.done1}>Требуемый темп:</div>
            </Grid>
            <Grid container xs={7} alignItems="center" justifyContent="center" direction="column">
                <Grid item className={classes.block_value}>
                    <div className={classes.value}>{((workDonePart2-workDonePart1)/deadline).toFixed(2)}</div>
                </Grid>
                <Grid item>
                    <div className={classes.done2}>М3/день</div>
                </Grid>
            </Grid>
        </Grid>
    );
}

export default NeedForSpeed;