import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {makeStyles} from "@material-ui/core/styles";

const useStylesCirc = makeStyles((theme) => ({
    root: {
        position: 'relative',
        flexGrow: 1,
    },
    bottom: {
        color: theme.palette.grey[theme.palette.type === 'light' ? 200 : 700],
    },
    circle: {
        strokeLinecap: 'round',
    },
    colorPrimary: {
        backgroundColor: 'rgba(182,188,226,0.6)',
    },
    topRed: {
        color: 'red',
        animationDuration: '550ms',
        position: 'absolute',
        left: 0,
    },

    topGreen: {
        color: 'green',
        animationDuration: '550ms',
        position: 'absolute',
        left: 0,
    },

    topOrange: {
        color: 'orange',
        animationDuration: '550ms',
        position: 'absolute',
        left: 0,
    },

}));

export default function CircularStatic({progress}) {
    const classes = useStylesCirc();

    return (
        <div className={classes.root}>
            <Box position="relative" display="inline-flex">
                <CircularProgress
                    variant="determinate"
                    className={classes.bottom}
                    size={110}
                    thickness={4}
                    value={100}
                />
                <CircularProgress
                    className={(() => {
                        if (progress < 40) {
                            return (classes.topRed)
                        }
                        else if (progress < 75) {
                            return (classes.topOrange)
                        }
                        else {
                            return (classes.topGreen)
                        };
                    })()
                    }
                    variant="determinate"
                    disableShrink
                    classes={{
                        circle: classes.circle,
                    }}
                    size={110}
                    thickness={4}
                    value={progress}
                />
                <Box
                    top={0}
                    left={0}
                    bottom={0}
                    right={0}
                    position="absolute"
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                >
                    <Typography variant="caption" component="div" color="textSecondary">{`${Math.round(
                        progress,
                    )}%`}</Typography>
                </Box>
            </Box>
        </div>
    );
}
