import '../../App.css';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom';
import {IconButton} from "@material-ui/core";
import 'date-fns';
import AddCircleIcon from "@material-ui/icons/AddCircle";
import SectionBlockMini from "../../components/SectionBlockMini";
import RoundProgressBar from "../../components/RoundProgressBar";
import DoneWork from "../../components/DoneWork";
import VerticalBar from "../../components/Chart/index.js";
import RemainBlock from "../../components/RemainBlock"
import NeedForSpeed from "../../components/NeedForSpeed";
import HeaderBlock from "../../components/HeaderBlock";
import Doughnut from "../../components/Doughnut";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },

    hrDown: {
        margin: "20px auto",
        borderTop: "1px solid rgba(0, 0, 0, 0.1)",
        width: "100%",
        border: 0,
        maxWidth: "45%",
        [theme.breakpoints.down('600')]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up('xs')]: {
            maxWidth: "80%",
        },
        [theme.breakpoints.up('md')]: {
            maxWidth: "60%",
        },
        [theme.breakpoints.up('lg')]: {
            maxWidth: "40%",
        },
    },

    UpperBlock: {
        background: 'rgb(22,111,255)',
        padding: 15,
        borderBottomRightRadius: 50,
    },

    header: {
        fontSize: 24,
        fontWeight: "bold",
        color: 'rgb(255,255,255)',
    },

    title: {
        fontSize: 15,
        fontWeight: "bold",
        textAlign: "left",
    },

    subtitle: {
        fontSize: 12,
        fontWeight: "bold",
        color: 'rgb(113,113,113)',
        textAlign: "left",
    },

    date_text: {
        fontSize: 12,
        color: 'rgb(113,113,113)',
        textTransform: 'uppercase',
    },

    date_value: {
        fontSize: 18,
        color: 'rgb(113,113,113)',
        fontWeight: "bold",
    },

    block: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },

    blockCircleProgress: {
      paddingLeft: 40,
    },

    chartBlock: {
      paddingTop: 20,
    },

    blockHeader: {
        background: 'rgb(229,239,255)',
        borderRadius: "15px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        padding: theme.spacing(1),
        margin: theme.spacing(1),
    },

}));

function StatVolumeWorkPage({ works }) {
    const classes = useStyles();
    const history = useHistory();

    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const idFromUrl = urlParams.get('id');

    function goToPage(id) {
        history.push("VolumeWorkPage?id=" + works[idFromUrl].id);
    }

    const progress = works[idFromUrl].workDonePart1/works[idFromUrl].workDonePart2 * 100;

    return (
        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <HeaderBlock/>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="center" className={classes.block}>
                            <Grid item lg={3} xs={8}>
                                <div className={classes.title}>{works[idFromUrl].title}</div>
                                <div className={classes.subtitle}>{works[idFromUrl].subtitle}</div>
                            </Grid>
                            <Grid item>
                                <IconButton aria-label="delete" onClick={goToPage}>
                                    <AddCircleIcon fontSize="large" style={{ color: 'rgb(22,111,255)' }}/>
                                </IconButton>
                            </Grid>
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <SectionBlockMini title={"Расценка"} value={works[idFromUrl].pricing}/>
                        <SectionBlockMini title={"Коэффициент"} value={works[idFromUrl].coeff}/>
                        <SectionBlockMini title={"Расценка с коэф. "} value={works[idFromUrl].pricingCoeff}/>
                        <hr className={classes.hrDown}/>
                        <Grid container className={classes.block} direction="row">
                            <DoneWork
                                workDonePart1={works[idFromUrl].workDonePart1}
                                workDonePart2={works[idFromUrl].workDonePart2}
                            />
                            <Grid item className={classes.blockCircleProgress}>
                                <RoundProgressBar progress={progress}/>
                            </Grid>
                        </Grid>
                        <Grid item className={classes.block}>
                            <Grid item xs={10} sm={7} lg={3} className={classes.chartBlock}>
                                <VerticalBar/>
                            </Grid>
                        </Grid>
                        <p/>
                        <Grid item className={classes.block}>
                            <RemainBlock
                            workDonePart1={works[idFromUrl].workDonePart1}
                            workDonePart2={works[idFromUrl].workDonePart2}
                            deadline={works[idFromUrl].deadline}
                            />
                        </Grid>
                        <Grid item className={classes.block}>
                            <NeedForSpeed
                                workDonePart1={works[idFromUrl].workDonePart1}
                                workDonePart2={works[idFromUrl].workDonePart2}
                                deadline={works[idFromUrl].deadline}
                            />
                        </Grid>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}
export default StatVolumeWorkPage;
