import '../../App.css';
import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import SectionBlockVolume from "../../components/SectionBlockVolume";
import HeaderBlock from "../../components/HeaderBlock";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },
}));

function OthersVolumeWorkPage({works}) {
    const classes = useStyles();

    return (
        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <HeaderBlock titleHeader="Набор работ на Июль 2021"/>
                        <Grid container justifyContent="center" alignItems="center" direction="column">
                            {works.map((obj) => {
                                return (
                                    <SectionBlockVolume
                                        id={obj.id}
                                        title={obj.title}
                                        subtitle={obj.subtitle}
                                        progress={obj.workDonePart1/obj.workDonePart2*100}
                                        date_begin={obj.date_begin}
                                        date_end={obj.date_end}
                                        quantity={obj.quantity}
                                        imgsArray={obj.imgsArray}
                                    />
                                )
                            })}
                        </Grid>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}
export default OthersVolumeWorkPage;
