import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({

    root: {
        flexGrow: 1,
    },

    block_value: {
        background: 'rgb(47,128,237)',
        borderRadius: "10px",
        padding:  theme.spacing(1),
        display: "flex",
        justifyContent: "center",
        width: "100%",
    },

    value : {
        fontSize: "18px",
        fontWeight: 'bold',
        color: 'rgb(255,255,255)',
    },

    done: {
        fontSize: 14,
        color: 'rgb(22,111,255)',
        textTransform: 'uppercase',
        fontWeight: 'bold',
        verticalAlign: "middle",
        textAlign: "left",
        padding: 3,
    },

    block: {
        background: 'rgb(229,239,255)',
        borderRadius: "15px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(2),
        margin: theme.spacing(1),
    },

}));

function DoneWork({ workDonePart1, workDonePart2 }) {
    const classes = useStyles();
    return (
        <Grid item className={classes.block} direction="column" xs={4} sm={2} lg={1}>
            <Grid item>
                <div className={classes.done}>Выполнено:</div>
            </Grid>
            <Grid item className={classes.block_value} xs={8} md={7} lg={7}>
                <div className={classes.value}>{workDonePart1}</div>
            </Grid>
            <Grid item>
                <div className={classes.done}>Из</div>
            </Grid>
            <Grid item className={classes.block_value} xs={8} md={7} lg={7}>
                <div className={classes.value}>{workDonePart2}</div>
            </Grid>
        </Grid>

    );
}

export default DoneWork;