import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";
import {IconButton} from "@material-ui/core";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import MenuIcon from "@material-ui/icons/Menu";
import {useHistory} from "react-router-dom";

const useStyles = makeStyles((theme) => ({

    root: {
        flexGrow: 1,
    },

    blockFlex: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },

    blockHeader: {
        background: 'rgb(22,111,255)',
        padding: 15,
        borderBottomRightRadius: 50,
    },

    hrHeader: {
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        width: "85%",
    },

    titleHeader: {
        fontSize: 20,
        fontWeight: "bold",
        color: 'rgb(255,255,255)',
    },

}));

function HeaderBlock({ titleHeader }) {
    const classes = useStyles();

    const history = useHistory()
    const goBack = () => {
        history.goBack()
    }

    return (
        <Grid container direction="column" className={classes.blockHeader}>
            <Grid container justifyContent="space-between">
                <Grid item>
                    <IconButton onClick={goBack}>
                        <ArrowBackIosIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                    </IconButton>
                </Grid>
                <Grid item className={classes.blockFlex}>
                    <div className={classes.titleHeader}>{titleHeader}</div>
                </Grid>
                <Grid item>
                    <IconButton>
                        <MenuIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                    </IconButton>
                </Grid>
            </Grid>
        </Grid>
    );
}

export default HeaderBlock;